* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
body {
  font-family: "Roboto", sans-serif;
  font-size: 1rem;
  line-height: 1.6;
  background-color: #fff;
  color: #333;
}
.container {
  max-width: 1000px;
  margin: 0 auto;
  padding-top: 1rem;
}
a {
  text-decoration: none;
}
a:hover {
  color: #666;
}
ul {
  list-style: none;
}
img {
  width: 100%;
}
.btn {
  display: inline-block;
  background: #333333;
  color: #fff;
  padding: 0.4rem 1.3rem;
  font-size: 1rem;
  border: none;
  cursor: pointer;
  margin-right: 0.5rem;
  transition: opacity 0.2s ease-in;
  outline: none;
}
.btn:disabled {
  background: #ccc;
  color: #000;
  cursor: not-allowed;
}
.btn:hover {
  opacity: 0.8;
}
.btn-tab {
  display: inline-flex;
  border-radius: 4px;
  font-size: 24px;
  margin-right: 10px;
  border-color: gray;
  border-style: solid;
  padding: 3px 7px;
}
.btn-tab.active {
  border-color: black;
}
.btn:hover {
  opacity: 0.8;
}
.btn.btn-danger {
  background: #dc3545;
  color: #fff;
}
.form-control {
  display: block;
  margin-top: 0.3rem;
}
.card {
  padding: 1rem;
  border: #ccc 1px dotted;
  margin: 0.7rem 0;
}
input,
select,
textarea {
  display: block;
  width: 100%;
  padding: 0.4rem;
  font-size: 1.2rem;
  border: 1px solid #ccc;
  margin: 1.2rem 0;
}
hr {
  margin: 0.5rem 0;
}
.form-control {
  display: block;
  margin-top: 0.3rem;
  margin-bottom: 0.5rem;
}
.form-control label {
  display: block;
  font-weight: bold;
  font-size: 1rem;
  margin-bottom: 0.5rem;
}
.form-control input {
  margin-bottom: 0;
}
.form-control .validation {
  color: red;
}